/** Media Queries **/
.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #1E293B;
  padding: 2rem;
}
.dashboard__header-grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (min-width: 1024px) {
  .dashboard__logo {
    text-align: left;
    margin: 0;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
}
.dashboard__submit--logout:hover {
  color: #007df4;
  cursor: pointer;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
  width: 12rem;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard__enlace {
  color: #6f8ab6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #40587f;
  color: #FFFFFF;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu-texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
}
.dashboard__contenido {
  padding: 1rem 1rem 1rem 1rem;
  width: 100%;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
.dashboard__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 5rem;
  }
}
.dashboard__boton {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 1.4rem;
}
.dashboard__boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-child(even) {
  background-color: #F8FAFC;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--acciones {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion--eliminar:hover {
  cursor: pointer;
}

.paginacion {
  margin: 5rem 0 3rem 0;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #007df4;
  font-size: 1.6rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid #007df4;
}
.paginacion__enlace--texto:hover {
  cursor: pointer;
  background-color: #007df4;
  color: #FFFFFF;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #1E293B;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.listado-ponentes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
.listado-ponentes__ponente, .listado-ponentes__ponente--seleccionado {
  color: #007df4;
  border: 1px solid #007df4;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
}
.listado-ponentes__ponente:hover, .listado-ponentes__ponente--seleccionado:hover {
  cursor: pointer;
}
.listado-ponentes__ponente--seleccionado {
  background-color: #007df4;
  color: #FFFFFF;
}